

export interface IAthlete {
  id?: number;
  firstName?: string;
  lastName?: string;
  surname?: string;
  birthDate?: Date;
  gender?: string;
  active?: boolean;
  country?: string;
  sport?: string;
  sizea?: string;
  sizeb?: string;
  sizec?: string;
}

export class Athlete implements IAthlete {

  constructor(
    public id: number,
    public firstName: string,
    public lastName: string,
    public surname: string,
    public birthDate: Date,
    public active: boolean,
    public country: string,
    public sport: string,
    public gender: string) {
  }

  fullName() {
    return `${this.firstName} ${this.lastName} ${this.surname}`;
  }

}
