import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LayoutComponent } from '@adminModule/containers/layout/layout.component';
import { HomeComponent } from '@adminModule/containers/home/home.component';
import { IndexComponent as IndexAthletesComponent } from '@adminModule/containers/athletes/index/index.component';
import { NewComponent as NewAthletesComponent } from '@adminModule/containers/athletes/new/new.component';

const routes: Routes = [
   { path: 'admin', component: LayoutComponent, children: [
       { path: 'athletes', component:  IndexAthletesComponent },
       { path: 'home', component:  HomeComponent },
       { path: 'athletes/new', component:  NewAthletesComponent },
       // {
       //  path: 'home', component: HomeComponent
       // }
     ]
   },
  // { path: '', redirectTo: 'register', pathMatch: 'full'  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
