import { Injectable } from '@angular/core';
import {Athlete, IAthlete} from '@adminModule/models/athlete.model';
import {SortColumn, SortDirection} from '@utils/directives/sortable.directive';
import {BehaviorSubject, Observable, of} from 'rxjs';

interface State {
  page: number;
  pageSize: number;
  searchTerm: string;
  sortColumn: SortColumn;
  sortDirection: SortDirection;
}

@Injectable({
  providedIn: 'root'
})
export class AthletesService {

  public loading = true;
  // tslint:disable-next-line:variable-name
  private _state: State = {
    page: 1,
    pageSize: 4,
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  };

  // listAthletes: Athlete[] = [];
  listAthletes$: BehaviorSubject<any[]> = new BehaviorSubject<any[]>([]);
  listAthletes: Athlete[] = [];
  constructor() {
    // const array = [];
    const a1 = new Athlete(
       1,
      'Diego Emilio',
      'Reyna',
      'Interian',
      new Date('1992-10-11'),
      true,
      'Puebla',
      'Remo',
      'Hombre'
    );
    this.listAthletes.push(a1);
    const a2 = new Athlete(
       2,
      'Juan Maximiliano',
      'Gonzalez',
      'Hernandez',
      new Date('1992-10-11'),
      false,
      'Yucatán',
      'Remo',
      'Hombre'
    );
    this.listAthletes.push(a2);
    const a3 = new Athlete(
       3,
      'Guadalupe De Los Angeles',
      'Cardenas',
      'Alvarez',
      new Date('1992-10-11'),
      false,
      'México',
      'Remo',
      'Mujer'
    );
    this.listAthletes.push(a3);
    const a4 = new Athlete(
       4,
      'Nadia Victoria',
      'Ake',
      'Gutierrez',
      new Date('1992-10-11'),
      false,
      'México',
      'Remo',
      'Mujer'
    );
    this.listAthletes.push(a4);
    const a5 = new Athlete(
       5,
      'Valeria Carolina',
      'Espinosa',
      'Moreno',
      new Date('1992-10-11'),
      false,
      'México',
      'Remo',
      'Mujer'
    );
    this.listAthletes.push(a5);
    // this.listAthletes$ = new BehaviorSubject<any[]>(array);

  }


  get page() { return this._state.page; }
  get pageSize() { return this._state.pageSize; }
  get searchTerm() { return this._state.searchTerm; }
  get sortDirection() { return this._state.sortDirection; }
  get sortColumn() { return this._state.sortColumn; }

  // tslint:disable-next-line:adjacent-overload-signatures
  set page(page: number) { this._set({page}); }
  // tslint:disable-next-line:adjacent-overload-signatures
  set pageSize(pageSize: number) { this._set({pageSize}); }
  // tslint:disable-next-line:adjacent-overload-signatures
  set searchTerm(searchTerm: string) { this._set({searchTerm}); }
  // tslint:disable-next-line:adjacent-overload-signatures
  set sortColumn(sortColumn: SortColumn) { this._set({sortColumn}); }
  // tslint:disable-next-line:adjacent-overload-signatures
  set sortDirection(sortDirection: SortDirection) { this._set({sortDirection}); }

  private _set(patch: Partial<State>) {
    Object.assign(this._state, patch);
    this.getRecordsFilter();
    // this.listAthletes$.next(this.getRecordsFilter());
  }

  getRecords(): Observable<any> {
    this.getRecordsFilter();
    return this.listAthletes$;
  }

  getTotalRecords(): Observable<any> {
    return of(this.listAthletes.length);
  }

  getRecordsFilter() {
    const records = [...this.listAthletes];

    const searchText = this._state.searchTerm.toLowerCase();
    let athletesSort = records.filter(athlete => {
      const statusText = (athlete.active) ? 'Activo' : 'Desactivado';
      return athlete.fullName().toLowerCase().includes(searchText) ||
        athlete.id.toString().includes(searchText) ||
        statusText.toLowerCase().includes(searchText) ||
        athlete.country.toString().includes(searchText) ||
        athlete.sport.toString().includes(searchText);
    });

    athletesSort = athletesSort.sort((athleteA, athleteB) => {
      if (this._state.sortDirection === 'asc') {
        return (athleteA[this._state.sortColumn] > athleteB[this._state.sortColumn]) ? -1 : 1;
      } else {
        if (this._state.sortDirection === 'desc') {
          return (athleteA[this._state.sortColumn] > athleteB[this._state.sortColumn]) ? 1 : -1;
        } else {
          return 1;
        }
      }
    });

    athletesSort = athletesSort.splice(((this._state.page - 1) * this._state.pageSize), this._state.pageSize);
    this.listAthletes$.next(athletesSort);
    // return athletesSort;
  }


}
