import {Component, OnInit, QueryList, ViewChildren} from '@angular/core';
import {Athlete} from '@adminModule/models/athlete.model';
import {Observable, of} from 'rxjs';
import {NgbdSortableHeaderDirective, SortColumns} from '@utils/directives/sortable.directive';
import {AthletesService} from '@adminModule/services/athletes.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-table-athletes',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.css']
})
export class TableComponent implements OnInit {
  athletes$: Observable<Athlete[]>;
  total$: Observable<number>;

  @ViewChildren(NgbdSortableHeaderDirective) headers: QueryList<NgbdSortableHeaderDirective>;

  constructor(public athleteService: AthletesService) {

    this.total$ = athleteService.getTotalRecords();
    athleteService.getRecords().subscribe(rs => {
      this.athletes$ = of(rs);
    });
  }

  ngOnInit(): void {
    this.athleteService.loading = false;
  }

  deletedEvent(nameAthlete: string) {
    // @ts-ignore
    Swal.fire({
      title: 'Eliminar información del deportista!',
      text: `¿Confirma que desea eliminar la información de ${nameAthlete}?`,
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      allowOutsideClick: false
    }).then((result) => {
      if (result.value) {
        // this.athleteService.fetchMoreDataTable();
        console.log('Refresh tabla');
      }
    });
  }

  onSort({column, direction}: SortColumns) {
    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    this.athleteService.sortColumn = column;
    this.athleteService.sortDirection = direction;
  }

}
