import { Component, OnInit } from '@angular/core';
import {IAthlete} from '@adminModule/models/athlete.model';
import {NgForm} from '@angular/forms';
import Swal from 'sweetalert2';
import {Router} from '@angular/router';

@Component({
  selector: 'app-form-athletes',
  templateUrl: './form-athletes.component.html',
  styleUrls: ['./form-athletes.component.css']
})
export class FormAthletesComponent implements OnInit {

  athlete: IAthlete = {};
  submitted = false;
  genderData: string[] = ['Hombre', 'Mujer'];
  sportData: string[] = ['Tiro con arco', 'Tenis de mesa', 'Remo', 'Pentatlon moderno'];
  countryData: string[] = ['Yucatán', 'Ciudad del carmen', 'Chiapas', 'Puebla'];
  constructor(private router: Router) { }

  ngOnInit(): void {
  }

  addAthlete(form: NgForm) {
    this.submitted = true;
    console.log('Formulario: ', form);
    if (form.valid) {
      Swal.fire({
        title: 'Exito!',
        text: `Se guardo correctamente los datos`,
        icon: 'success',
        showCancelButton: false,
        confirmButtonText: 'Confirmar',
        allowOutsideClick: false
      }).then((result) => {
        if (result.value) {
          this.router.navigate(['/admin/athletes']);
          // this.athleteService.fetchMoreDataTable();
        }
      });
    }
  }

}
