import {Directive, EventEmitter, Input, NgModule, Output} from '@angular/core';
import {Athlete} from '@adminModule/models/athlete.model';

export type SortColumn = keyof Athlete | '';
export type SortDirection = 'asc' | 'desc' | '';
const rotate: {[key: string]: SortDirection} = { asc: 'desc', desc: '', '': 'asc' };

export interface SortColumns {
  column: SortColumn;
  direction: SortDirection;
}

@Directive({
  // tslint:disable-next-line:directive-selector
  selector: 'th[sortable]',
  // tslint:disable-next-line:no-host-metadata-property
  host: {
    '[class.asc]': 'direction === "asc"',
    '[class.desc]': 'direction === "desc"',
    '(click)': 'rotate()'
  }
})
export class NgbdSortableHeaderDirective {

  @Input() sortable: SortColumn = '';
  @Input() direction: SortDirection = '';
  @Output() sort = new EventEmitter<SortColumns>();

  rotate() {
    this.direction = rotate[this.direction];
    this.sort.emit({column: this.sortable, direction: this.direction});
  }
}

@NgModule({
  declarations: [ NgbdSortableHeaderDirective ],
  exports: [ NgbdSortableHeaderDirective ]
})

export class NgbdSortableHeaderDirectiveModule {}
