import {Component, Input, OnInit} from '@angular/core';
import {Card} from '@adminModule/models/card.model';

@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.css']
})
export class CardComponent implements OnInit {

  @Input() card: Card;

  constructor() { }

  ngOnInit(): void {
  }

  downloadFile() {
    const url = window.URL.createObjectURL('parameter');
    window.open(url);
  }

}
